  font-size: 2rem;\n}\n\n.container-restaurant .restaurant__info h3{\n  color:black;\n  font-weight: 600;\n  font-size: 1.5rem;\n  text-align: center;\n}\n\n.container-restaurant .restaurant__info p{\n  margin-bottom: 40px;\n  margin-top: 10px;\n  font-size: 1.1rem;\n  letter-spacing: 1px;\n  line-height: 25px;\n  color: black;\n  text-align: center;\n}\n\n.container-restaurant .restaurant__info .menus{\n  display: grid;\n  grid-template-columns: repeat(auto-fit,minmax(250px,1fr));\n  gap: 10px;\n  padding: 2rem 1rem;\n}\n\n.container-restaurant .restaurant__info .menus .foods,.drinks{\n  padding-inline: 10px;\n}\n\n.container-restaurant .restaurant__info .menus .foods h4,.drinks h4{\n  margin-block: 15px;\n  font-size: 1.1rem;\n}\n\n.container-restaurant .restaurant__info .menus .foods ul,.drinks ul{\n  display: grid;\n  grid-template-columns: repeat(auto-fit,minmax(235px,1fr));\n  gap: 15px;\n}\n\n.container-restaurant .restaurant__info .menus .foods ul li,.drinks ul li{\n  list-style-type: square;\n  padding: 16px 20px;\n  border-radius: 6px;\n  color: #000;\n  font-weight: 500;\n  font-size: 1rem;\n  box-shadow: 0 0 5px black;\n  background-color: #f1ede5;\n}\n\n.container-customer-reviews{\n  width: 100%;\n}\n\n.container-customer-reviews h2{\n  text-align: center;\n  text-decoration: underline #43766C;\n  margin-bottom: 10px;\n  font-weight: 600;\n  font-size: 1.5rem;\n}\n\n.container-customer-reviews .restaurant__customer-reviews .cards{\n  display: grid;\n  grid-template-columns: repeat(auto-fit,minmax(250px,1fr));\n  gap: 15px;\n}\n\n.container-customer-reviews .restaurant__customer-reviews .cards .card__customer-review{\n  background-color: #f1ede5;\n  padding: 1rem 1.5rem;\n  border-radius: 6px;\n  box-shadow: 0 0 5px black;\n}\n\n.container-customer-reviews .restaurant__customer-reviews .cards .card__customer-review h3{\n  color: #000;\n  font-weight: 600;\n  margin-bottom: 10px;\n  font-size: 1.1rem;\n}\n\n.container-customer-reviews .restaurant__customer-reviews .cards .card__customer-review p{\n  color: #000;\n  font-size: 1rem;\n}\n\n.container-input-review{\n  width: 100%;\n}\n\nmain .container .floating-form {\n  margin-block-start: 1.5rem;\n  padding: 1rem 2rem;\n}\n\nmain .container .floating-form h2{\n  text-align: center;\n  text-decoration: underline #43766C;\n  margin-bottom: 10px;\n  font-weight: 600;\n  font-size: 1.5rem;\n}\n\nmain .container .floating-form .input-form {\n  background-color: #f1ede5;\n  padding: 2rem;\n  border-radius: 5px;\n  -webkit-border-radius: 5px;\n  -moz-border-radius: 5px;\n  -ms-border-radius: 5px;\n  -o-border-radius: 5px;\n  box-shadow: #000 0 1px 2px 0, #000 0 1px 3px 1px;\n  display: flex;\n  flex-direction: column;\n  text-align: left;\n  gap: 20px;\n}\nmain .container .floating-form .input-form .form-group label {\n  font-weight: 700;\n  font-size: 1.1rem;\n}\nmain .container .floating-form .input-form .form-group .name {\n  min-height: 44px;\n  min-width: 44px;\n  width: 100%;\n  margin-top: 10px;\n  font-size: 1rem;\n  border: none;\n  box-shadow: #000 0 4px 8px -2px, #000 0 0 0 1px;\n  padding: 5px;\n}\nmain .container .floating-form .input-form .form-group .body {\n  padding: 5px;\n  width: 100%;\n  height: 180px;\n  margin-top: 7px;\n  font-size: 1rem;\n  border: none;\n  box-shadow: #000 0 4px 8px -2px, #000 0 0 0 1px;\n}\nmain .container .floating-form .input-form button {\n  cursor: pointer;\n  padding-block: 8px;\n  border: none;\n  font-weight: 700;\n  background-color: #43766C;\n  color: #fbfbfb;\n  border-radius: 4px;\n  transition: 0.15s all ease-in-out;\n  -webkit-transition: 0.15s all ease-in-out;\n  -moz-transition: 0.15s all ease-in-out;\n  -ms-transition: 0.15s all ease-in-out;\n  -o-transition: 0.15s all ease-in-out;\n  border: 2px solid #fbfbfb;\n  min-width: 44px;\n  min-height: 44px;\n}\nmain .container .floating-form .input-form button:hover {\n  border: 2px solid #43766C;\n  background-color: #fbfbfb;\n  color: #43766C;\n}\n/* Like */\n\n.like 